@import '../../libs/normalize';
@import '../../variables';

.wrapper {
  width: 100%;
  height: 196px;
  display: flex;
  align-items: center;
  padding: 0 120px;
  background-color: #000000;

  .inner {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .links_block {
      margin-left: calc(200px);
      width: fit-content;
      position: absolute;
      margin: 0 calc((100vw - 1046px) / 2);

      @media (max-width: 1440px) {
        position: absolute;
        margin: 0 180px;
      }

      ul {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr 2fr;
        column-gap: 80px;
        width: fit-content;

        @media (max-width: 1360px) {
          column-gap: 24px;
        }

        li {
          margin-bottom: 32px;
          font-weight: 200;
          font-size: 20px;
          color: white;
          &:nth-last-of-type(-n + 4) {
            margin-bottom: 0;
          }

          &:nth-of-type(4) {
            width: fit-content;
          }

          .link {
            font-weight: 200;
            font-size: 20px;
            color: white;
            text-decoration: none;

            @media (max-width: 1300px) {
              font-size: 18px;
            }
          }
        }
      }
    }
  }

  .lng {
    font-weight: 200;
    font-size: 20px;
    color: white;
    width: 32px;
    height: 32px;
    display: flex;
    justify-content: center;
    align-items: center;

    &:hover {
      text-decoration: underline;
    }

    @media (max-width: 1460px) {
      font-size: 16px;
    }
  }

  .set_lng {
    display: flex;
    position: relative;
    width: 188px;
    justify-content: flex-end;

    .lng_item {
      @extend .lng;
      margin-right: 16px;
      width: fit-content;
    }
  }
}

.wrapper_lk {
  width: 100%;
  height: 196px;
  display: flex;
  align-items: center;
  padding: 0 120px;
  background-color: #000000;

  @media (max-width: 1460px) {
    padding: 0 80px;
  }

  .links_block_lk {
    margin-left: 298px;
    width: 890px;
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 1650px) {
      width: 650px;
      margin-left: 150px;
    }

    .col {
      display: flex;
      flex-direction: column;
      height: 88px;
      justify-content: space-between;

      .text {
        font-weight: 600;
        font-size: 20px;
        color: white;
      }

      .item {
        font-weight: 200;
        font-size: 20px;
        color: white;
      }
    }
  }
}
